@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500&family=Nova+Square&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab+Highlight&display=swap);
body{box-sizing:border-box;margin:0;font-family:"Noto Sans JP", sans-serif}.content-container{position:absolute;display:flex;width:820px;align-items:center;flex-direction:column;left:50%;transform:translateX(-50%)}.title-content{position:relative;width:100%;height:auto;margin-bottom:4.5em}#puzzle-title{position:relative;width:auto;text-align:center;font-size:3em;margin-top:1.5em;font-family:"Nova Square", cursive}#title-underline{position:relative;display:block;margin:0 auto;width:calc(100% - 100px);height:8px;background-color:red}.puzzle-info{position:relative;width:820px;margin-bottom:3em}.puzzle-info .info-title{font-weight:bold;font-size:1.7em;margin-bottom:10px;width:auto;text-align:center}.puzzle-info .info-body{font-size:1.2em;padding-left:0.2em;text-align:center}.hex-container{position:relative;display:grid;justify-content:center;width:820px;height:820px;margin-bottom:5em;grid-template-columns:260px 260px 260px;grid-template-rows:260px 260px 260px}.hexagon{position:absolute;display:inline-block;height:260px;width:300px;transform-origin:50% 50%}.hexagon .triangle{position:absolute;width:0px;height:0px;left:75px;border-style:solid;border-radius:10px;border-width:130px 75px 0;border-color:#19c transparent;transform-origin:75px 133px}.hexagon .triangle:nth-child(1){transform:rotate(60deg)}.hexagon .triangle:nth-child(2){transform:rotate(120deg)}.hexagon .triangle:nth-child(3){transform:rotate(180deg)}.hexagon .triangle:nth-child(4){transform:rotate(240deg)}.hexagon .triangle:nth-child(5){transform:rotate(300deg)}#hex-0{grid-column-start:2;grid-row-start:2}#hex-1{grid-column-start:2;grid-row-start:1;top:-10px}#hex-2{grid-column-start:3;grid-row-start:1;transform:translate(-20px, 120px)}#hex-3{grid-column-start:3;grid-row-start:3;transform:translate(-20px, -120px)}#hex-4{grid-column-start:2;grid-row-start:3;top:10px}#hex-5{grid-column-start:0;grid-row-start:3;transform:translate(40px, -120px)}#hex-6{grid-column-start:0;grid-row-start:1;transform:translate(40px, 120px)}#solve-button{position:relative;display:block;width:175px;height:3em;padding:0.3em 1.2em;margin:0 0.1em 0.1em 0;border:0.16em solid rgba(255,255,255,0);border-radius:2em;box-sizing:border-box;text-decoration:none;font-family:"Roboto", sans-serif;font-weight:bold;font-size:1.2em;color:white;background-color:#9a4ef1;text-shadow:0 0.04em 0.04em rgba(0,0,0,0.35);text-align:center;line-height:2em;font-size:1.5em;transition:all 0.8s;margin-bottom:100px}#solve-button:hover{cursor:pointer;border-color:#0c0b0b}@media (max-width: 820px){.content-container{left:0;transform:translateX(0)}}


